#searchIcon, .searchList{
    width: 100%; 
    height: 59px;
    /* border-radius: 29px; */
    /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
    color: var(--desactive-text);
    padding: 0 5px 0 63px;
    background-color: var(--second-main);
}

#searchIcon::placeholder{
    color: var(--desactive-text);
    /* background-color: var(--second-main); */
}
.search input:-webkit-autofill,
.search input:-webkit-autofill:hover, 
.search input:-webkit-autofill:focus, 
.search input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px var(--second-main) inset !important;
    -webkit-text-fill-color:var(--white)!important;
}
#searchIcon{
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: var(--main-border);
}
.cont{
    position: relative;    
    /* border-width: 0 0 1px 0;
    border-style: solid;
    border-color: var(--main-border); */
}

.loupe{
    position: absolute;
    left: 23px;
    top:18px;
    background-color: var(--second-main);
}

.search{
    width: 100%;
    /* margin-bottom: 10px; */
}

.searchList{
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: var(--main-border);
}
.searchList{
    background-color: var(--second-main);
    /* margin-bottom: 30px; */
    /* max-height: 100px; */
    /* padding:0 5px 15px 21px; */
    /* padding-right: 0; */
    padding-left: 0;
    height: 100%;
}

/* .searchCont{
    padding: 13px 28px 21px 22px;
} */

.list-content{
    padding-bottom: 20px;
}

.list{
    display: flex;
    align-items: center;
    padding: 14px 5px 14px 21px;
    /* padding: 13px 0 ; */
}

.user img{
    width: 48px;
    height: 48px;
    border-radius: 50%;
}
.user{
    width: 48px;
    height: 48px;
    border-radius: 50%;
}
.name{
    margin-left: 15px;
    color: var(--white);
}
.searchListName{
    max-height: 100px;
    /* padding: 15px 0; */
}
.container{
    height: 100%;
}
.containerSearchList{
    overflow-y: auto;
    padding-bottom: 30px;
}
.list:hover{
    background-color: #252632;
}
/* .inputSearch{
    border: 1px solid transparent;
}
.err{
    border:1px solid var(--red);
} */


@media screen and (max-width:770px){
    #searchIcon{
        height: 45px;
        font-size: 12px;
        padding-left: 55px;
    }
    .loupe{
        top:calc(50% - 10px)
    }
    .loupe img{
        width: 17px;
        height: 17px;
    }
    .searchList{
        padding: 14px;
    }
    .user{
        width: 30px;
        height: 30px;
    }
    .list{
        padding:7px 14px 6.5px 11px;
    }
    .searchList{
        padding: 0 3px 0 0 ;
    }
    .name{
        font-size: 12px;
    }
    #searchIcon{
        font-size: 12px;
        color:var(--white);
    }
}